import React from "react"
import PropTypes from "prop-types";
import { graphql, Link } from "gatsby"
import Img from "gatsby-image"

import Layout from "components/layout"
import SEO from "components/seo"
import Card from "components/card"
import Title from "components/title"

export const queryImage = graphql`
    query {
        projeto1: file(relativePath: { eq: "gatsby-course.jpg" }) {
            childImageSharp {
                fixed(width: 96, height: 96) {
                    ...GatsbyImageSharpFixed
                }
            }
        }
    }
`;

const Projects = ({data}) => (
    <Layout>
        <SEO 
            pageTitle="Projetos" 
            description="Desenvolvedor Web"
            keywords="Desenvolvedor, PHP, WEB, André, Silva, André Silva"
        />
        <section className="section is-size-4-desktop is-size-5-touch">
            <Title>Projetos</Title>
            <p className="has-text-centered">
                Meus projetos 
                {' '}
                <a rel="noopener noreferrer" href="https://github.com/andresilvabr" target="_blank">GitHub</a>
            </p>

            <div className="columns is-centered">
                <div className="column is-half">
                    <Card 
                        title="Curso Gatsby"
                        subtitle="Aprendendo Gatsbyjs/react"
                        link=""
                        tags={["Gatsby","React"]}
                        image={<Img 
                            fixed={data.projeto1.childImageSharp.fixed}
                            alt="Curso Gatsby"
                        />}
                    />
                </div>
            </div>
            <p className="has-text-centered">Visite meu <Link to="/blog">blog</Link>.</p>
        </section>
    </Layout>
)

Projects.propTypes = {
    data: PropTypes.object.isRequired
}

export default Projects